.logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 85%;
  max-width: 750px;
}

.content {
  width: 95%;
  max-width: 666px;
  margin-left: auto;
  margin-right: auto;
}

.dataEntry {
  padding: 10px;
  border: 3px solid green;
  border-radius: 3px;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
}

.totalCostCents {
  width: 100%;
  max-width: 300px;
  margin: 10px;
}

.smallDataEntry {
  width: calc(100% - 20px);
  max-width: 300px;
  margin: 10px;
}

.largeDataEntry {
  width: calc(100% - 20px);
  margin: 10px;
}

.stampValues {
  margin-top: 20px;
}

.generateSolution {
  width: calc(100% - 20px);
  max-width: 300px;
  margin: 10px;
  height: 40px;
  border-radius: 3px;
  font-weight: 700;
  display: inline-block;
  background-color: green;
  &:hover {
    background-color: rgb(17, 155, 17);
  }
}

.resetOptions {
  width: calc(100% - 20px);
  max-width: 300px;
  margin: 10px;
  height: 40px;
  border-radius: 3px;
  font-weight: 700;
  display: inline-block;
  background-color: gray;
  &:hover {
    background-color: lightslategray;
  }
}

.footerText {
  font-size: 11pt;
  width: 100%;
  display: inline-block;
  text-align: center;
  height: 30px;
  margin-top: -1px;

  a:link {
    text-decoration: none;
  }

  a:visited {
    text-decoration: none;
  }

  a:hover {
    text-decoration: none;
  }

  a:active {
    text-decoration: none;
  }
}

.footer {
  margin-top: -10px;
  margin-bottom: 10px;
  border-top: 3px solid green;
  height: 30px;
  line-height: 30px;
}

.whiteTextButton {
  color: white;
  text-align: center;
  line-height: 40px;
  font-family: Roboto, Helvetica, Arial, sans-serif;
  font-size: 13pt;
}

.postageSolutionUnsaved {
  background-color: rgb(92, 172, 92);
  border-radius: 10px;
  width: 360px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;

  /* &:hover {
    background-color: rgb(17, 155, 17);

    .stampContainerUnsaved {
      background-image: radial-gradient(rgb(17, 155, 17), 50%, transparent 50%);
    }
  } */
}

.postageSolutionSaved {
  background-color: rgb(17, 155, 17);
  border-radius: 10px;
  width: 360px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;

  /* &:hover {
    background-color: rgb(92, 172, 92);

    .stampContainerSaved {
      background-image: radial-gradient(rgb(92, 172, 92), 50%, transparent 50%);
    }
  } */
}

.stampContainerUnsaved {
  background-color: #fff;
  background-image: radial-gradient(rgb(92, 172, 92), 50%, transparent 50%);
  background-position: -0.25rem -0.25rem;
  background-repeat: repeat;
  background-size: 0.5rem 0.5rem;
  height: 88px;
  width: 72px;
  display: inline-block;
  margin: 5px;
}

.stampContainerSaved {
  background-color: #fff;
  background-image: radial-gradient(rgb(17, 155, 17), 50%, transparent 50%);
  background-position: -0.25rem -0.25rem;
  background-repeat: repeat;
  background-size: 0.5rem 0.5rem;
  height: 88px;
  width: 72px;
  display: inline-block;
  margin: 5px;
}

.stamp {
  width: 63px;
  height: 78px;
  background-color: white;
  font-weight: 700;
  display: inline-block;
  margin-left: 5px;
  margin-top: 5px;
  line-height: 80px;
  text-align: center;
}

.solutionButtonHelper {
  width: 360px;
  margin-left: auto;
  margin-right: auto;
}

.footerSeperator {
  /* height: 6px; */
  height: 3px;
  width: 100%;
  background-color: green;
  border-radius: 3px;
  display: block;
}
